* {
  position: relative;
  vertical-align: top;
}

body {
  background: #f4f4f4;
  margin: 0;
}

.l-sidebar {
  display: inline-block;
  width: 300px;
  height: 100%;
  margin: 0;
  background: rgba(254, 254, 254, 1);
  border-right: 1px solid #f4f4f4;
  position: fixed;
  z-index: 100;
  overflow: hidden;
}

.l-main {
  padding-left: 310px;
  display: inline-block;
}

.btn {
  position: relative;

  display: inline-block;
  margin: 30px auto;
  padding: 10px 20px;

  overflow: hidden;

  border-width: 0;
  outline: none;
  border-radius: 2px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.6);

  background-color: #2ecc71;
  color: #ecf0f1;

  -webkit-transition: background-color 0.3s;

  transition: background-color 0.3s;
}

.btn:hover,
.btn:focus {
  background-color: #27ae60;
}

.btn > * {
  position: relative;
}

.btn span {
  display: block;
  padding: 12px 24px;
}

.btn:before {
  content: "";

  position: absolute;
  top: 50%;
  left: 50%;

  display: block;
  width: 0;
  padding-top: 0;

  border-radius: 100%;

  background-color: rgba(236, 240, 241, 0.3);

  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.btn:active:before {
  width: 120%;
  padding-top: 120%;

  -webkit-transition: width 0.2s ease-out, padding-top 0.2s ease-out;

  transition: width 0.2s ease-out, padding-top 0.2s ease-out;
}
